*{
    margin: 0;
    padding: 0;
}


.name{
    font-family: Madimi One;
    font-size: 50px;
    line-height: 1.5cm;
    width: 10cm;
    text-align: center;
    // font-weight: 600;
    color: #e9feff;
}

.welcome{
    color: #e9feff;

}

.home-upper{
    padding: 3cm 10vw;
    position: relative;

    &::before{
        position: absolute;
        content: "";
        inset: 0;
        background-image: url("back.jpg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        z-index: -1;
        filter: brightness(45%);
    }
}


